"use client"
import { Playlist } from "@/types/Playlist"
import { useEffect, useState } from "react"
import getCookies from "../helpers/get-cookies"

export default function Builder() {
    const [playlists, setPlaylists] = useState<Playlist[]>([])

    useEffect(() => {
        const fetchPlaylists = async () => {
            const cookies = getCookies();
            const response = await fetch(`${process.env.NEXT_PUBLIC_BACKEND_URL}/users/${cookies.userId}/playlists`, {
                headers: {
                    Authorization: `Bearer ${cookies.accessToken}`
                }
            })
            const playlists = await response.json()
            console.log('Setting playlists: ', playlists)
            setPlaylists(playlists)
        }
        fetchPlaylists()
    }, [])

    return (
        <div>
            <p className="text-purple-300">Select one of your playlists to begin</p>
            <div className="text-white">
                {playlists.map(playlist => (
                    <Card>
                        
                    </Card>
                ))}
            </div>
        </div>

    )
}